<template>
	<div>
		<p>{{turn}}님의 턴입니다.</p>
		<table-component :table-data="tableData" />
		<div v-if="winner">{{winner}}님의 승리입니다!</div>
	</div>
</template>

<script>
	import TableComponent from './TableComponent';
	import EventBus from './EventBus';

	export default {
		components: {
			TableComponent,
		}, // 자식 컴포넌트가 있을 경우에 여기에 적는다.
		props: {}, // 자식 컴포넌트일 경우 부모에게서 받을 프롭스를 지정한다. 데이터 형식도 적어둔다.
	  data() {
	    return {
	    	tableData: [
	    		['', '', '',],
			    ['', '', '',],
			    ['', '', '',],
		    ],
		    turn: 'O',
		    winner: '',
	    };
	  }, // 컴포넌트의 화면상에 드러나는 정보들을 데이터로 만들어둔다.
		computed: {}, // 가공되는 데이터들은 여기에 넣어서 사용하는 것이 좋다. 캐싱 때문이다.
	  methods: {
			onClickTd(rowIndex, cellIndex) { // 넘겨받은 데이터는 여기에서 인자로 활용할 수 있다.

				// this.$root.$data.tableData[this.rowIndex][this.cellIndex] = this.$root.$data.turn;
				// 이렇게 하면 값이 변한다. 하지만 렌더링은 되지 않는다.
				// 배열이나 객체의 내부를 인덱스를 통한 변화는 렌더링되지 않는다. 그래서 아래처럼 해야한다.
				// $set은 이러한 뷰의 한계를 보완해준다.

				this.$set(this.tableData[rowIndex], cellIndex, this.turn);

				let win = false;
				if (this.tableData[rowIndex][0] === this.turn &&
					this.tableData[rowIndex][1] === this.turn &&
					this.tableData[rowIndex][2] === this.turn) {
					win = true;
				}
				if (this.tableData[0][cellIndex] === this.turn &&
					this.tableData[1][cellIndex] === this.turn &&
					this.tableData[2][cellIndex] === this.turn) {
					win = true;
				}
				if (this.tableData[0][0] === this.turn &&
					this.tableData[1][1] === this.turn &&
					this.tableData[2][2] === this.turn) {
					win = true;
				}
				if (this.tableData[0][2] === this.turn &&
					this.tableData[1][1] === this.turn &&
					this.tableData[2][0] === this.turn) {
					win = true;
				}

				if (win) { // 이겼을 때.
					this.winner = this.turn;
					this.turn = 'O';
					this.tableData = [['', '', '',], ['', '', '',], ['', '', '',]];
				} else { // 무승부였을 때.
					let all = true;
					this.tableData.forEach((row) => {
						row.forEach((cell) => {
							if (!cell) {
								all = false;
							}
						});
					});

					if (all) {
						this.winner = '';
						this.turn = 'O';
						this.tableData = [['', '', '',], ['', '', '',], ['', '', '',]];
					}
					this.turn = this.turn === 'O' ? 'X' : 'O';
				}
			},
	  }, // 데이터가 사용되는 함수들은 이 안에 만들어서 사용한다. 데이터를 사용하지 않는다면 밖에다 만들어도 좋다.
		beforeCreate() {}, // 정보가 준비되기 직전
		created() {
			EventBus.$on('clickTd', this.onClickTd); // 이벤트버스에 등록.
		}, // 컴포넌트가 렌더링 되기 전, 정보를 준비하는 상태
		beforeMount() {}, // 렌더링 되기 직전
		mounted() {}, // 정보를 기반으로 컴포넌트가 렌더링 된 상태
		beforeUpdate() {}, // 리렌더링 되기 직전
		updated() {}, // 렌더링 된 상태에서 정보가 업데이트 되고 리렌더린 된 상태
		beforeDestroy() {}, // 컴포넌트가 사라지기 직전
		destroyed() {}, // 컴포넌트가 효과를 다 해 사라진 상태
		watch: {}, // 데이터로 사용하고 있는 값의 변화를 주시하다가 변하면 이 안의 코드가 자동적으로 실행되는데, 웬만해선 쓰지 않는 게 좋다.

	};
</script>

<style>
	table {
		border-collapse: collapse;
	}

	td {
		border: 1px solid #333333;
		width: 50px;
		height: 50px;
		text-align: center;
	}
</style>